import React, { useState, useEffect } from 'react';
import {
  TextField,
  Autocomplete,
  Box,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent,
  CircularProgress
} from '@mui/material';
import { stockAPI } from '../services/api';

interface StockSearchProps {
  onStockSelect: (symbol: string, market: string) => void;
}

interface StockOption {
  symbol: string;
  name: string;
}

const StockSearch: React.FC<StockSearchProps> = ({ onStockSelect }) => {
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [stockOptions, setStockOptions] = useState<StockOption[]>([]);
  const [selectedStock, setSelectedStock] = useState<StockOption | null>(null);
  const [market, setMarket] = useState<string>('us');
  const [loading, setLoading] = useState<boolean>(false);

  //   
  const defaultStocks = {
    us: [
      { symbol: 'AAPL', name: 'Apple Inc.' },
      { symbol: 'GOOGL', name: 'Alphabet Inc.' },
      { symbol: 'MSFT', name: 'Microsoft Corporation' },
      { symbol: 'TSLA', name: 'Tesla, Inc.' },
      { symbol: 'AMZN', name: 'Amazon.com Inc.' },
      { symbol: 'NVDA', name: 'NVIDIA Corporation' },
      { symbol: 'META', name: 'Meta Platforms Inc.' },
      { symbol: 'NFLX', name: 'Netflix Inc.' }
    ],
    kr: [
      { symbol: '005930.KS', name: '' },
      { symbol: '000660.KS', name: 'SK' },
      { symbol: '051910.KS', name: 'LG' },
      { symbol: '035420.KS', name: 'NAVER' },
      { symbol: '035720.KS', name: '' },
      { symbol: '207940.KS', name: '' },
      { symbol: '006400.KS', name: 'SDI' },
      { symbol: '068270.KS', name: '' }
    ]
  };

  useEffect(() => {
    //      
    const stocks = market === 'kr' ? defaultStocks.kr : defaultStocks.us;
    setStockOptions(stocks);
    setSelectedStock(null);
    setSearchQuery('');
  }, [market]);

  useEffect(() => {
    if (searchQuery.length > 1) {
      searchStocks(searchQuery);
    } else {
      const stocks = market === 'kr' ? defaultStocks.kr : defaultStocks.us;
      setStockOptions(stocks);
    }
  }, [searchQuery, market]);

  const searchStocks = async (query: string) => {
    setLoading(true);
    try {
      const response = await stockAPI.searchStocks(query);
      if (response.results) {
        setStockOptions(response.results);
      }
    } catch (error) {
      console.error('  :', error);
      //     
      const stocks = market === 'kr' ? defaultStocks.kr : defaultStocks.us;
      setStockOptions(stocks);
    } finally {
      setLoading(false);
    }
  };

  const handleStockSelect = (event: any, newValue: StockOption | null) => {
    setSelectedStock(newValue);
    if (newValue) {
      onStockSelect(newValue.symbol, market);
    }
  };

  const handleMarketChange = (event: SelectChangeEvent) => {
    setMarket(event.target.value);
  };

  return (
    <Box sx={{ mb: 3 }}>
      <Typography variant="h4" component="h1" gutterBottom align="center">
        AI  
      </Typography>
      <Typography variant="body1" color="text.secondary" align="center" sx={{ mb: 3 }}>
          AI  
      </Typography>
      
      <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', flexWrap: 'wrap' }}>
        <FormControl sx={{ minWidth: 120 }}>
          <InputLabel id="market-select-label"></InputLabel>
          <Select
            labelId="market-select-label"
            id="market-select"
            value={market}
            label=""
            onChange={handleMarketChange}
          >
            <MenuItem value="us"></MenuItem>
            <MenuItem value="kr"></MenuItem>
          </Select>
        </FormControl>

        <Autocomplete
          sx={{ flex: 1, minWidth: 300 }}
          options={stockOptions}
          getOptionLabel={(option) => `${option.symbol} - ${option.name}`}
          value={selectedStock}
          onChange={handleStockSelect}
          inputValue={searchQuery}
          onInputChange={(event, newInputValue) => {
            setSearchQuery(newInputValue);
          }}
          loading={loading}
          renderInput={(params) => {
            const { InputProps, ...otherParams } = params;
            return (
              <TextField
                {...otherParams}
                label={market === 'kr' ? '  ' : '  '}
                placeholder={market === 'kr' ? ': , 005930' : ': Apple, AAPL'}
                variant="outlined"
                InputProps={{
                  ...InputProps,
                  endAdornment: (
                    <>
                      {loading ? <CircularProgress color="inherit" size={20} /> : null}
                      {InputProps?.endAdornment}
                    </>
                  ),
                }}
              />
            );
          }}
          renderOption={(props, option) => (
            <Box component="li" {...props}>
              <Box>
                <Typography variant="body1" fontWeight="bold">
                  {option.symbol}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {option.name}
                </Typography>
              </Box>
            </Box>
          )}
        />
      </Box>
    </Box>
  );
};

export default StockSearch;